var isArray = Array.isArray;
export default {
  name: 'table-tree-column',
  props: {
    prop: {
      type: String
    },
    treeKey: {
      type: String,
      default: 'id'
    },
    parentKey: {
      type: String,
      default: 'parentId'
    },
    levelKey: {
      type: String,
      default: '_level'
    },
    childKey: {
      type: String,
      default: 'children'
    }
  },
  methods: {
    childStyles: function childStyles(row) {
      return {
        'padding-left': (row[this.levelKey] > 1 ? row[this.levelKey] * 8 : 0) + 'px'
      };
    },
    iconClasses: function iconClasses(row) {
      return [!row._expanded ? 'el-icon-caret-right' : 'el-icon-caret-bottom'];
    },
    iconStyles: function iconStyles(row) {
      return {
        visibility: this.hasChild(row) ? 'visible' : 'hidden'
      };
    },
    hasChild: function hasChild(row) {
      return isArray(row[this.childKey]) && row[this.childKey].length >= 1 || false;
    },
    // 切换处理
    toggleHandle: function toggleHandle(index, row) {
      var _this = this;

      if (this.hasChild(row)) {
        var data = this.$parent.store.states.data.slice(0);
        /* eslint no-underscore-dangle: 0 */

        data[index]._expanded = !data[index]._expanded;

        if (data[index]._expanded) {
          data = data.splice(0, index + 1).concat(row[this.childKey]).concat(data);
        } else {
          data = this.removeChildNode(data, row[this.treeKey]);
        }

        this.$parent.store.commit('setData', data);
        this.$nextTick(function () {
          _this.$parent.doLayout();
        });
      }
    },
    // 移除子节点
    removeChildNode: function removeChildNode(data, parentId) {
      var parentIds = isArray(parentId) ? parentId : [parentId];

      if (parentId.length <= 0) {
        return data;
      }

      var ids = [];

      for (var i = 0; i < data.length; i++) {
        if (parentIds.indexOf(data[i][this.parentKey]) !== -1 && parentIds.indexOf(data[i][this.treeKey]) === -1) {
          ids.push(data.splice(i, 1)[0][this.treeKey]);
          i--;
        }
      }

      return this.removeChildNode(data, ids);
    }
  }
};